<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>厂家三包</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">返胎出库</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看返胎出库</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="params" ref="TableDataRef">
        <el-form-item label="库存组织" size="mini">
          <el-input v-model="params.company_name" disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="供应商(厂家)" size="mini">
          <el-input v-model="params.supplier_name" disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="业务日期" size="mini">
          <el-date-picker v-model="params.business_time" disabled="disabled" type="date" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="所属区域" size="mini" prop="cityList">
          <span>{{params.province_name}}&nbsp;&nbsp;{{params.city_name}}&nbsp;&nbsp;{{params.area_name}}</span>
        </el-form-item>
        <el-form-item label="详细地址" size="mini" prop="address">
          <el-input v-model="params.address" disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="收货人" size="mini" prop="consignee_name">
          <el-input v-model="params.consignee_name" disabled="disabled" maxLength="7"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" size="mini" prop="consignee_mobile">
          <el-input v-model="params.consignee_mobile" disabled="disabled"></el-input>
        </el-form-item>

        <el-form-item label="库存部门" size="mini">
          <el-input v-model="params.department_name" disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="出库类型" size="mini">
          <template>
            <span>返胎出库</span>
          </template>
        </el-form-item>
        <el-form-item label="摘要" size="mini">
          <el-input v-model="params.remark" type="text" disabled="disabled" maxlength="300"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData" max-height="600" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column :show-overflow-tooltip="true" property="brand_name" width="150" label="品牌名称">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" width="150" label="商品编号">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" min-width="500" label="商品名称">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="warehouse_name" width="132" label="出库仓库">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" width="120" label="出库货位">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" width="160" label="返胎数量" align="right">
          <template slot-scope="scope">
            {{scope.row.goods_num=1}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="tire_un" label="胎号" width="180">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property=" " label=" ">
        </el-table-column>
      </el-table>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">返 回</el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import {
  getCompanyListCommon,
  getDepartmentListCommon,
  bindIdsF,
  supplierListOpen //获取供应商下拉
} from "common/utils/public_function/commonUtils";
import getAreaList from "common/components/public/getAreaList";
import appraisalDalog from "./authenticate_dalog.vue";
export default {
  name: "aftersaleAuditreeoutexamine",
  data() {
    return {
      authority: {},
      TableData: [],
      salesDocumentsV: false,
      multipleSelection: [],
      params: {
        cityList: []
      },
      departmentList: {
        options: []
      },
      supplierList: [],
      defaultArea: [0], //默认的区域id
      companyList: [],
      from: ""
    };
  },
  components: {
    getAreaList, //区域列表
    appraisalDalog //销售单据
  },
  computed: {
    ...mapGetters(["auditreeoutExamine_Get", "auditreeoutIndex_Get"])
  },
  methods: {
    ...mapActions(["treeoutreeIndexACT"]),
    goBack() {
      // wz annotation cache
      // this.treeoutreeIndexACT({
      //   // moduleName: "Index",
      //   // params: this.auditreeoutExamine_Get.params,
      //   params: this.auditreeoutIndex_Get
      //     ? this.auditreeoutIndex_Get.params
      //     : {},
      //   goPage: this.auditreeoutExamine_Get.goPage,
      //   pageSize: this.auditreeoutExamine_Get.pageSize
      // });
      this.$router.push({
        path: "/aftersale/treeoutree"
      });
    },
    dongList(val) {
      if (val.length) {
        if (this.params.outstock_type == "1") {
          let batchIdArr = this.TableData.map(item => {
            return item.company_appraisal_goods_id;
          });
          val.forEach(item => {
            if (!batchIdArr.includes(item.company_appraisal_goods_id)) {
              this.TableData.push(item);
            } else {
              this.$message({
                message: "此数据已引用",
                type: "error"
              });
            }
          });
        } else {
          let batchIdArr = this.TableData.map(item => {
            return item.company_appraisal_goods_id;
          });
          val.forEach(item => {
            if (!batchIdArr.includes(item.company_appraisal_goods_id)) {
              this.TableData.push(item);
            } else {
              this.$message({
                message: "此数据已引用",
                type: "error"
              });
            }
          });
        }
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    supplier() {
      let options = {
        company_id: this.params.company_id,
        supplier_name: null
      };
      supplierListOpen(this, options);
    },
    submit(formName) {
      let obj = [];
      if (!this.params.supplier_id) {
        this.$message({
          message: "请选择供应商",
          type: "error"
        });
        return false;
      }
      this.TableData.forEach((item, index) => {
        obj.push({
          factory_appraisal_goods_id: item.factory_appraisal_goods_id,
          goods_id: item.goods_id,
          warehouse_id: item.warehouse_id,
          allocation_id: item.allocation_id,
          goods_num: item.goods_num
        });
      });
      this.$refs[formName].validate(valid => {
        if (valid) {
          let datas = {
            ...this.params,
            goods_json: JSON.stringify(obj)
          };
          if (this.auditreeoutExamine_Get.reborn_outstock_id) {
            this.$axMethod.editRebornOutstock(datas).then(response => {
              if (response.data.code == 200) {
                this.$message({
                  message: response.data.data,
                  type: "success"
                });
                this.goBack();
              }
            });
          } else {
            //新增
            this.$axMethod.addRebornOutstock(datas).then(response => {
              if (response.data.code == 200) {
                this.$message({
                  message: response.data.data,
                  type: "success"
                });
                this.goBack();
              }
            });
          }
        }
      });
    },
    deletes() {
      if (this.multipleSelection.length) {
        let a = new Set(this.TableData);
        let b = new Set(this.multipleSelection);
        let differenceABSet = new Set([...a].filter(x => !b.has(x)));
        this.TableData = Array.from(differenceABSet);
      } else {
        this.$message({
          message: "请先选择要删除的数据！",
          type: "warning"
        });
      }
    },
    quoteOrder() {
      //引用单据
      if (this.params.supplier_id) {
        this.salesDocumentsV = true;
      } else {
        this.$message({
          message: "请选择供应商(厂家)",
          type: "warning"
        });
      }
    },
    companyChange(val) {
      if (val == 1) {
        getDepartmentListCommon(this, this.params); //不清除后所有级
      } else {
        this.$set(this.params, "supplier_id", "");
        getDepartmentListCommon(this, this.params, 1); //清楚后所有级
      }
    },
    changeDefaultArea(addressId) {
      this.params.cityList = addressId.join(",");
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    supplierChange(val) {
      getCompanyListCommon(this); //获取组织
      this.$axMethod
        .getSupplierInfo({
          supplier_id: val
        })
        .then(res => {
          if (res.data.code == 200) {
            if (res.data.data.province_id) {
              this.defaultArea = [
                res.data.data.province_id,
                res.data.data.city_id,
                res.data.data.area_id
              ];
              this.params.cityList = this.defaultArea.join(",");
              this.params.province_id = this.defaultArea[0];
              this.params.city_id = this.defaultArea[1];
              this.params.area_id = this.defaultArea[2];
            }
          }
          let obj = {
            consignee_name: res.data.data.principal_name,
            consignee_mobile: res.data.data.principal_mobile,
            address: res.data.data.address
          };

          let paramsObj = Object.assign({}, obj, this.params);
          this.$set(this, "params", paramsObj);
          this.companyChange(1); //获取部门
        });
    }
  },
  mounted() {
    getCompanyListCommon(this); //获取组织
    let vm = this;
    if (this.auditreeoutExamine_Get.reborn_outstock_id) {
      this.$axMethod
        .getRebornOutstockInfo({
          reborn_outstock_id: this.auditreeoutExamine_Get.reborn_outstock_id
        })
        .then(res => {
          if (res.data.code == 200) {
            this.params = res.data.data.info;
            this.TableData = res.data.data.goods_list;
            this.defaultArea = [
              res.data.data.info.province_id,
              res.data.data.info.city_id,
              res.data.data.info.area_id
            ];
            bindIdsF(this, res.data.data.info, [
              ["supplier_id", "supplier_name", "supplierList"],
              ["department_id", "department_name", "departmentList", "options"]
            ]);
            this.params.cityList = this.defaultArea.join(",");
            this.params.province_id = this.defaultArea[0];
            this.params.city_id = this.defaultArea[1];
            this.params.area_id = this.defaultArea[2];
          }
        });
    }
    this.companyChange(1); //获取部门 联动获取客户的下拉

    let { employee_id, company_admin_id } = localStorage;
    if ([0, 1, 2].includes(Number(employee_id)) || company_admin_id > 0) {
      this.$set(this, "params", {
        business_time: this.$publicFution.formatData(new Date()),
        company_id: Number(localStorage.company_id)
      });
    } else {
      this.$set(this, "params", {
        business_time: this.$publicFution.formatData(new Date()),
        company_id: Number(localStorage.company_id),
        department_id: Number(localStorage.department_id)
      });
    }
  }
};
</script>

